<template>
    <v-row class="qiong-pt-5 mt-3">
        <v-col class="px-0 qiong-pt-15 pb-0" cols="12">
            <qiong-title :size_class="'qiong-txt-22'">
                {{ $store.state.bonus.user.display_name }}
            </qiong-title>
        </v-col>

        <v-col class="pa-0 qiong-td mt-2" cols="12">

            <div class="qiong-wide-27 ">
                <qiong-equal-content class="fw-b qiong-txt-tr" :second_class="'ec-mb-2'">
                    <div slot="label">會員編號</div>
                    <div slot="content">{{ $store.state.bonus.user.number_code }} </div>
                </qiong-equal-content>

                <qiong-equal-content :second_class="'ec-mb-1'">
                    <div slot="label">用戶名稱</div>
                    <div slot="content">{{ $store.state.bonus.user.username }}</div>
                </qiong-equal-content>

                <qiong-equal-content :second_class="'ec-mb-1'">
                    <div slot="label">職位</div>
                    <div slot="content"><level-viewing :item="$store.state.bonus.vip"></level-viewing></div>
                </qiong-equal-content>

            </div>
            <div class="qiong-wide-23" v-if="me">
                <qiong-equal-content class="fw-b qiong-txt-tr" :first_class="'ec-wide-6chart'" :second_class="'ec-mb-2'">
                    <div slot="label">本周支付級別</div>
                    <div slot="content">{{ $store.state.bonus.user.number_code }} </div>
                </qiong-equal-content>

                <qiong-equal-content :first_class="'ec-wide-6chart'">
                    <div slot="label">個人點數</div>
                    <div slot="content">{{ me.ipv }} </div>
                </qiong-equal-content>

                <qiong-equal-content :first_class="'ec-wide-6chart'">
                    <div slot="label">團隊點數</div>
                    <div slot="content">{{ me.son_pv }} </div>
                </qiong-equal-content>

                <qiong-equal-content :first_class="'ec-wide-6chart'">
                    <div slot="label">合資格下線</div>
                    <div slot="content">{{ me.son_can_num }} </div>
                </qiong-equal-content>

                <qiong-equal-content :first_class="'ec-wide-6chart'">
                    <div slot="label">領取獎金資格</div>
                    <div slot="content"> {{ bonus.has.team_layer(me.ipv) }} </div>
                </qiong-equal-content>

            </div>
            <div class="qiong-wide-23" v-else></div>

            <div class="qiong-wide-50 pr-0">
                <as-top-msg-order :_me="me" v-if="me"></as-top-msg-order>
            </div>
        </v-col>
    </v-row>
</template>

<script>
import QiongEqualContent from '../../../../components/Qiong/Pager/QiongEqualContent.vue'
import QiongTitle from '../../../../components/Qiong/Ui/QiongTitle.vue'
import LevelViewing from '../../../../components/Viewing/LevelViewing.vue'
import AsTopMsgOrder from './AsTopMsgOrder.vue'
    export default {
        props: {
            me: {
                type: Object
            }
        },
        components: {
            QiongTitle,
                LevelViewing,
                QiongEqualContent,
                AsTopMsgOrder 
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
.as-wide-4chart
    width: calc( 45px + 2vw )
.as-wide-6chart
    width: calc( 100px + 4vw )
</style>


