<template>
    <div>
        <div class="qiong-tr ec-mb-2">
            <div class="qiong-wide-35">領取獎金資格訂單日期</div>
            <div class="qiong-wide-17">會員編號</div>
            <div class="qiong-wide-22">Role</div>
            <div class="qiong-wide-14">訂單編號</div>
            <div class="qiong-wide-12 text-right pr-0">訂單點數</div>
        </div>
        <div class="qiong-td"
            v-for="(v, i) in buys" :key="i"
        >
            <div class="qiong-wide-35">{{ view.ser_timed(v.buy_time, true) }}</div>
            <div class="qiong-wide-17">{{ v.origin_buyer_number_code }}</div>
            <div class="qiong-wide-23">{{ bonus.has.order_role(v.role) }}</div>
            <div class="qiong-wide-15">{{ v.order_code }}</div>
            <div class="qiong-wide-10 text-right pr-0">{{ v.num * v.product.pv }}PV</div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            _me: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                buys: null
            }
        },
        mounted() {
            this.buysLoading()
        },
        methods: {
            async buysLoading() {
                const buys = await this.conn.get(
                    this.api.buys,
                    this.$store.state.token,
                    {
                        'chronu': this.$store.state.chronus.id,
                        'users_permissions_user': this._me.id
                    }
                )


                if (buys) {
                    
                    this.buys = buys.filter(e => {
                        const pv = this.view.floatMul(e.num, e.product.pv)
                        if (this.bonus.has.team_layer(pv)) {
                            return true
                        }
                        return false
                    })
                    console.log(this.buys)
                }
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


